import {
  NgModule,
} from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { INFO_ROUTES} from './info.routes';
import { MyOrderComponent} from './myOrder';
import { UserInfoComponent} from './userInfo';
import { SecurityComponent} from './security';

export const infoComponents = [
  SecurityComponent,
  UserInfoComponent,
  MyOrderComponent
]

@NgModule({
  declarations: infoComponents,
  imports: [ // import Angular's modules
    FormsModule,
    RouterModule.forChild(INFO_ROUTES)
    
  ],
  providers: [ // expose our Services and Providers into Angular's dependency injection

],
exports: [infoComponents,RouterModule]
})
export class InfoModule {



}



